import React, { Component } from 'react';
import { connect } from 'react-redux';

import './FooterContent.scss';
import { IoCaretDown, IoCaretUp } from 'react-icons/io5';

class FooterContent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isShow: false,
        };
    }

    handelClickMore = () => {
        this.setState({
            isShow: !this.state.isShow,
        });
    };
    render() {
        let { isShow } = this.state;

        return (
            <div className="footer-content-container ">
                <div className="btn-more-content" onClick={this.handelClickMore}>
                    <p>Vai tr ca BookingCare</p>
                    <span className="icon-more">{isShow ? <IoCaretUp /> : <IoCaretDown />}</span>
                </div>

                {isShow && (
                    <div className="content-footer">
                        <p>
                            <strong>Gip bnh nhn chn ng bc s gii v t lch nhanh chng.</strong>
                        </p>
                        <ul>
                            <li>H thng bc s chuyn khoa gii, uy tn</li>
                            <li>Thng tin v bc s  c xc thc r rng, chnh xc</li>
                            <li>Sp xp khm ng bc s m bnh nhn  chn t lch</li>
                            <li>Bo v quyn li ca bnh nhn khi i khm</li>
                            <li>Min ph t lch.</li>
                        </ul>
                        <p>
                            <strong>H tr trc, trong v sau khi i khm.</strong>
                        </p>
                        <p>Trc khm</p>
                        <ul>
                            <li>Nhc lch khm, dn d chun b trc khm</li>
                            <li>Hng dn i li, quy trnh lm th tc khm</li>
                        </ul>
                        <p>Trong khi khm</p>
                        <ul>
                            <li>H tr gii quyt cc vng mc trong khi khm</li>
                            <li>H tr ngi bnh nhng yu cu ny sinh</li>
                        </ul>
                        <p>Sau khi khm</p>
                        <ul>
                            <li>Ghi nhn  kin ca bnh nhn sau khm</li>
                            <li>H tr gii p, lm r nhng vn  chuyn mn</li>
                            <li>Bo v quyn li ca bnh nhn khi i khm</li>
                        </ul>
                    </div>
                )}
            </div>
        );
    }
}

const mapStateToProps = () => {
    return {};
};

const mapDispatchToProps = () => {
    return {};
};

export default connect(mapStateToProps, mapDispatchToProps)(FooterContent);
